import React from "react";
import { Link } from "react-router-dom";

export default function Header() {
  return (
    // <header className="bg-black text-white px-8 py-4 flex items-center lm- justify-between">
    <header className="bg-000000 px-8 py-4 flex items-center justify-between w-[1300px] mx-auto">

      {/* Left - Logo */}
        <div className="text-white flex flex-col leading-none text-[32px] tracking-wide">
        <span>URBANZI</span>
        <span>SOLUTIONS</span>
        </div>


        {/* Center - Nav */}
        <nav className="flex-1">
        <table className="table-auto border-collapse w-full">
            <tbody>
            <tr className="text-white text-sm uppercase tracking-wide text-center hover:[&>td]:text-white cursor-pointer [&>td]:w-[110px] [&>td]:px-4 [&>td]:py-2">
                {/* <td>Home</td> */}
                <td className="w-[110px] px-4 py-2 cursor-pointer text-sm uppercase tracking-wide transition-transform duration-300 hover:scale-110">
                <Link to="/home" className="">
                  Home
                </Link>
                </td>

                <td className="">About Us</td>
                <td>Services</td>
                <td>Our Client</td>
                <td>Careers</td>
                <td>Product</td>
                <td>Blog</td>
                <td>Contact Us</td>
            </tr>
            </tbody>
        </table>
        </nav>

      {/* Right - Button */}
      <button className="bg-white text-black px-5 py-2 rounded-sm text-xs font-semibold uppercase hover:bg-gray-300">
        Let's Talk
      </button>
    </header>
  );
}
